import Vue from 'vue'
import Router from 'vue-router'
// import App from '@/layout/App.vue'

Vue.use(Router)

const home = r => require.ensure([], () => r(require('@/pages/home.vue')), 'group1')
const index = r => require.ensure([], () => r(require('@/pages/index.vue')), 'group2')
const article = r => require.ensure([], () => r(require('@/pages/article.vue')), 'group2')
const d3 = r => require.ensure([], () => r(require('@/pages/d3.vue')), 'group2')
const maps = r => require.ensure([], () => r(require('@/pages/map.vue')), 'group2')
const details = r => require.ensure([], () => r(require('@/pages/details.vue')), 'group3')

const routes = [
  {
    path: '/',
    redirect: '/article',
    component: home,
    children: [
      { path: '/home', name: 'index', component: index, meta: { page: 0 } },
      { path: '/article', name: 'art', component: article, meta: { page: 0 } },
      { path: '/d3', name: 'D3 demo', component: d3, meta: { page: 1 } },
      { path: '/map', name: 'map', component: maps, meta: { page: 2 } },
      { path: '/show', name: 'details', component: details, meta: { page: 0 } }
    ]
  }
]

const router = new Router({
  mode: 'hash',
  base: __dirname,
  routes
})

export default router



