/**
*
* @export
* @param {any} time 输入需要转化的时间参数
* @param {any} format 需要转化的格式
* @returns 返回处理后的时间格式
*/
export function dateFormat (time, format, g) {
  // const between = (Date.now() - Number(time)) / 1000
  // if (between < 3600 && ((between / 60) < 1)) {
  //   return '刚刚'
  // } else if (between < 3600) {
  //   return pluralize(~~(between / 60), '分钟')
  // } else if (between < 86400) {
  //   return pluralize(~~(between / 3600), '小时')
  // }
  if (!time) return null
  var date = new Date(time)
  var o = {
    'M+': date.getMonth() + 1, // month
    'd+': date.getDate(), // day
    'h+': date.getHours(), // hour
    'm+': date.getMinutes(), // minute
    's+': date.getSeconds(), // second
    'q+': Math.floor((date.getMonth() + 3) / 3), // quarter
    'S': date.getMilliseconds() // millisecond
  }
  let sss = format
  if (/(y+)/.test(format)) {
    if (new Date().getFullYear() !== date.getFullYear() || g === 1) {
      format = format.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
    } else {
      format = format.replace(RegExp.$1, '')
    }
  }
  for (var k in o) {
    if (new RegExp('(' + k + ')').test(format)) {
      format = format.replace(RegExp.$1, RegExp.$1.length === 1 ? o[k] : ('00' + o[k]).substr(('' + o[k]).length))
    }
  }
  if (/(y+)/.test(sss) && new Date().getFullYear() === date.getFullYear() && !g) {
    format = format.substr(1)
  }
  return format
}

