import axios from './axios'

export function init () {
  return axios.get('/info')
              .then(res => res.data)
}

export function getArtList (params) {
  return axios.get('/getArtList', { params })
              .then(res => res.data)
}

export function getLabel (params) {
  return axios.get('/getLabel', { params })
              .then(res => res.data)
}

export function showDetail (params) {
  return axios.get(`/getArtShow/${params}`)
              .then(res => res.data)
}



