import service from '@/api/index'
import { notice } from '@/utils/res'

const state = {
  list: [],
  label: [],
  details: {}
}

const actions = {
  async getArtList ({ commit }, data) {
    const res = await service.getArtList(data)
    if (res.code === 1) commit('LIST', res.result)
    else notice(res.message)
  },

  async getLabel ({ commit }, data) {
    const res = await service.getLabel(data)
    if (res.code === 1) commit('LABEL', res.result)
    else notice(res.message)
  },

  async showDetail ({ commit }, data) {
    const res = await service.showDetail(data)
    if (res.code === 1) commit('DETAILS', res.result)
    else notice(res.message)
  }
}

const mutations = {
  LIST (state, data) {
    state.list = data
  },
  LABEL (state, data) {
    state.label = data
  },
  DETAILS (state, data) {
    state.label = data
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations
}



